import React, { useState } from "react";
import Navpan from "./navpan";
import './courses.css';
import Courselist from "./courselist";
import Mycourselist from "./my_courses";
function Courses() {
    
    const [showModal, setShowModal] = useState(false);
    const [selectedNav, setSelectedNav] = useState('all');

    const handleImageClick = () => {
        setShowModal(true);
    }

    const handleNavClick = (nav) => {
        setSelectedNav(nav);
    }

    const closeModal = () => {
        setShowModal(false);
    }

    return(
        <>
        <div className="containercourses">
        <div className="header">
            <div className="coursesh"></div>
           
            {showModal && (
                <div className="modal">
                    <button onClick={closeModal}></button>
                    <p>   !</p>
                </div>
            )}
        </div>
        <div className="linewithnav">
        
            <div className={`textnav ${selectedNav === 'all' ? 'active' : ''}`} onClick={() => handleNavClick('all')}></div>
            <div className={`textnav ${selectedNav === 'my' ? 'active' : ''}`} onClick={() => handleNavClick('my')}></div>
            <div className={`textnav ${selectedNav === '  ' ? 'active' : ''}`} onClick={() => handleNavClick('  ')}> </div>
            <div className={`textnav ${selectedNav === ' ' ? 'active' : ''}`} onClick={() => handleNavClick(' ')}> </div>
        </div>


    
        {selectedNav === 'my' && <Mycourselist />}
        {selectedNav && <Courselist selectedNav={selectedNav} />}
        </div>
        <Navpan />
        
      </>
    )
}

export default Courses;
