import React, { useState } from "react";
import { jwtDecode } from 'jwt-decode';
import { BASE_URL } from './config';
import axios from 'axios';
function Authcode() {
    const token = localStorage.getItem('token');
    const decodedToken = jwtDecode(token);
    const id = decodedToken.sub;
    const [emailModal, setEmailModal] = useState(false); 
    const [isLoading, setIsLoading] = useState(false);

    const [unicCode, setUnicCode] = useState(''); 
    const handleCloseEmail = () => {
          setEmailModal(false)
          };
    
          
    const handleGetAuthCode = async () => {
        setEmailModal(true)
        setIsLoading(true)
      const data = {
        id: id
      };
      
        try {
          const response = await axios.post(
            `${BASE_URL}tourschoolphp/authcode.php`,
            JSON.stringify(data),
            {
              headers: {
                'Content-Type': 'application/json',
              },
            }
          );
      
          if (response.data.success) {
            setIsLoading(false)
           console.log("   ");
           setUnicCode(response.data.unic_code)
       
          } else {
            console.error('Error sending message:', response.data.error);
          }
        } catch (error) {
    if (error.response) {
        //  
        console.error('Server Error:', error.response.data);
    } else if (error.request) {
        //    
        console.error('Request Error:', error.request);
    } else {
        //  
        console.error('Error:', error.message);
    }
}
      };

    return(
        <>
                <div className="knopkaApplication" onClick={handleGetAuthCode}> <div className="applicationtextprof">  </div>
        </div>


{emailModal && (
    <>
<div id="myModal" className="modalunic">

<div className="modal-contentunic">
<span className="closeemail" onClick={handleCloseEmail}></span>
{isLoading ? (
          <div className="loading-spinner">
            <div className="spinner"></div>
            <span>...</span>
          </div>
        ) : (
            <>
    <img src="/vercodestud.svg" className="svgemail"></img>
    
<div className="vercodetext">    :</div>
<div className="vercodecode">{unicCode}</div>
<div className="vercodenot">    ,        </div>
</>
        )}
</div>


</div>
</>
)}
</>

    )
    
}
export default Authcode;